import React, { useState } from "react";
import Modal from "../common/commonmodal"; // Modal  
import { useDispatch } from "react-redux"; //   
import { logoutUser } from "../../redux/store/module/authModule"; //   
import { useNavigate } from "react-router-dom"; //    

const Menubar = () => {
    const [modalType, setModalType] = useState("");
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const openModal = (type) => {
        setModalType(type);
    };

    const closeModal = () => {
        setModalType("");
    };

    const handleLogout = () => {
        dispatch(logoutUser()); //      
        navigate("/login"); //     
    };

    return (
        <div className="menubar">
            <button
                onClick={() => openModal("Rank")}
                className="menubar-button"
            >
                
            </button>
            <button
                onClick={() => openModal("MyPage")}
                className="menubar-button"
            >
                 
            </button>
            <button
                onClick={() => openModal("Shop")}
                className="menubar-button"
            >
                
            </button>
            <button onClick={handleLogout} className="menubar-button">
                
            </button>{" "}
            {/*    */}
            {modalType && <Modal type={modalType} closeModal={closeModal} />}
        </div>
    );
};

export default Menubar;
