import { useState } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import TimerRadio from "./TimerRadio";
import TimerRadioGroup from "./TimerRadioGroup";
import axios from "axios";
import { create } from "../../redux/store/module/waiting";
axios.defaults.withCredentials = true;
export default function CreateRoom({ socket }) {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [pwInput, setPwInput] = useState("");

    const handleNewRoom = async (e) => {
        e.preventDefault();

        const r_name = e.target.roomTitle.value;
        const r_password = e.target.roomPw.value.trim();

        if (r_name.trim() === "") {
            alert("    .");
        } else {
            try {
                //      POST 
                const res = await axios.post(
                    `http://localhost:8080/api-server/room`,
                    {
                        r_name,
                        r_password,
                    }
                );

                //  ,  
                const userId = res.data.userId;
                const roomId = res.data.roomId;

                //    Redux store 
                dispatch(
                    create({
                        r_name: r_name,
                        room_id: roomId,
                        user_id: userId,
                    })
                );

                socket.emit("createRoom", r_name, r_password, userId);
                socket.on("err", (errMsg) => {
                    alert(errMsg);
                    setPwInput("");
                    r_name = "";
                });

                // useEffect(() => {
                //     //   
                //     socket.on("newRoomList", (r_name, r_status) => {
                //         // {room_id, r_name, r_status, user_id}
                //         dispatch(
                //             create({
                //                 r_name: r_name,
                //                 room_id: nextID,
                //                 user_id: Number(socket.id), //  socket.id  
                //                 r_status: r_status,
                //             })
                //         );
                //         console.log(`${r_name}   `);

                //         //      
                //         postWaitingList(r_name, r_status);
                //     });
                // }, []);

                //   
                navigate("/tetris");
            } catch (error) {
                console.error("Error creating new room:", error);
            }
        }
    };

    const checkSpacebar = (e) => {
        if (e.code === "Space") {
            setPwInput("");
            alert(" space  .");
        }
    };

    return (
        <div className="CreateRoom">
            {/*  */}
            <div className="modal">
                <div className="modal-body">
                    <form className="newRoom" onSubmit={handleNewRoom}>
                        <label>
                             
                            <input
                                type="text"
                                className="roomTitle"
                                name="roomTitle"
                                placeholder=" "
                            />
                        </label>
                        <br />
                        {/* <label>
                             
                            <TimerRadioGroup label="">
                                <TimerRadio
                                    name="time"
                                    value="sec30"
                                    defaultChecked
                                >
                                    00:30
                                </TimerRadio>
                                <TimerRadio name="time" value="sec60">
                                    01:00
                                </TimerRadio>
                                <TimerRadio name="time" value="sec90">
                                    01:30
                                </TimerRadio>
                            </TimerRadioGroup>
                        </label>
                        <br /> */}
                        <label>
                            
                            <input
                                type="text"
                                name="roomPw"
                                className="roomPw"
                                value={pwInput.trim()}
                                onKeyDown={checkSpacebar}
                                onChange={(e) => setPwInput(e.target.value)}
                                placeholder=""
                            />
                        </label>
                        <label>
                            <button type="submit" className="newRoomSubmit">
                                 
                            </button>
                        </label>
                    </form>
                </div>
            </div>
        </div>
    );
}
