import axios from "axios";
axios.defaults.withCredentials = true;
// Action Types
const REGISTER_SUCCESS = "auth/REGISTER_SUCCESS";
const REGISTER_FAIL = "auth/REGISTER_FAIL";
const LOGIN_SUCCESS = "auth/LOGIN_SUCCESS";
const LOGIN_FAIL = "auth/LOGIN_FAIL";
const LOGOUT = "auth/LOGOUT";
const LOGOUT_SUCCESS = "auth/LOGOUT_SUCCESS";
const LOGOUT_FAIL = "auth/LOGOUT_FAIL";

// Initial State
const initialState = {
    userData: null,
    error: null,
};

// Reducer
export default function authReducer(state = initialState, action) {
    switch (action.type) {
        case REGISTER_SUCCESS:
            return {
                ...state,
                userData: action.payload,
                error: null,
            };
        case REGISTER_FAIL:
            return {
                ...state,
                error: action.payload,
            };
        case LOGIN_SUCCESS:
            return {
                ...state,
                userData: action.payload,
                error: null,
            };
        case LOGIN_FAIL:
            return {
                ...state,
                error: action.payload,
            };
        case LOGOUT:
            return {
                ...state,
                userData: null,
            };
        default:
            return state;
    }
}

// Action Creators
export const registerUser = (email, password, nickname) => async (dispatch) => {
    try {
        const response = await axios.post(
            `${process.env.REACT_APP_API_SERVER}/auth/register`,
            { email, password, nickname }
        );
        dispatch({ type: REGISTER_SUCCESS, payload: response.data });
        alert(response.data.msg);
    } catch (error) {
        dispatch({ type: REGISTER_FAIL, payload: error.response.data });
    }
};
// loginUser  
export const loginUser = (email, password) => async (dispatch) => {
    try {
        const response = await axios.post(
            `${process.env.REACT_APP_API_SERVER}/auth/login`,
            {
                email,
                password,
            }
        );

        if (response.data.result) {
            //   userId email 
            const userData = {
                userId: response.data.userId,
                email: response.data.email,
            };
            dispatch({ type: LOGIN_SUCCESS, payload: userData });

            //     
            localStorage.setItem("isLoggedIn", "true");
            localStorage.setItem("user", JSON.stringify(userData));
            console.log("");
        } else {
            return Promise.reject(response.data.msg); //   
        }
    } catch (error) {
        dispatch({
            type: LOGIN_FAIL,
            payload: error.response
                ? error.response.data
                : "Login failed: Network error",
        });
        return Promise.reject(error);
    }
};

// loginUserFromLocalStorage  
export const loginUserFromLocalStorage = (user) => ({
    type: LOGIN_SUCCESS,
    payload: {
        userId: user.userId,
        email: user.email,
    },
});

export const logoutUser = () => async (dispatch) => {
    try {
        //   
        await axios.get(`${process.env.REACT_APP_API_SERVER}/auth/logout`);
        //   isLoggedIn  user  
        localStorage.removeItem("isLoggedIn");
        localStorage.removeItem("user");
        //    
        dispatch({ type: LOGOUT_SUCCESS });
    } catch (error) {
        //    
        console.error("Logout failed:", error);
        dispatch({
            type: LOGOUT_FAIL,
            payload: error.response ? error.response.data : "Unknown Error",
        });
    }
};
