import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { registerUser } from "../../redux/store/module/authModule";
import axios from "axios";

axios.defaults.withCredentials = true;
const RegisterModalContent = ({ closeModal }) => {
    const dispatch = useDispatch();

    //   state
    const [email, setEmail] = useState("");
    const [nickname, setNickname] = useState("");
    const [password, setPassword] = useState("");
    const [checkPassword, setCheckPassword] = useState("");

    //         state
    const [emailValidCheck, setEmailValidCheck] = useState(false);
    const [emailDuplicateCheck, setEmailDuplicateCheck] = useState(false);
    const [nickValidCheck, setNickValidCheck] = useState(false);
    const [nickDuplicateCheck, setNickDuplicateCheck] = useState(false);
    const [pwValidCheck, setPwValidCheck] = useState(false);
    const [confirmPw, setConfirmPw] = useState(false);

    //     state
    const [emailValidMessage, setEmailValidMessage] = useState("");
    const [nickValidMessage, setNickValidMessage] = useState("");
    const [pwValidMessage, setPwValidMessage] = useState("");
    const [pwConfirmMessage, setPwConfirmMessage] = useState("");
    //       409   => axiosInstance.post
    const axiosInstance = axios.create({
        validateStatus: function (status) {
            return status >= 200 && status < 500; //   200  500    
        },
    });
    //   
    const checkDuplicate = async (type, value) => {
        //      
        if (type === "email") {
            if (email === "") {
                alert("  .");
            } else if (!emailValidCheck) {
                alert(" .   .");
                return;
            } else {
                const response = await axiosInstance.post(
                    `${process.env.REACT_API_SERVER}/auth/emailDuplicate`,
                    { email: value }
                );
                alert(response.data.msg);
                if (!response.data.result) {
                    setEmailDuplicateCheck(false);
                } else {
                    setEmailDuplicateCheck(true);
                }
            }
            //      
        } else if (type === "nickname") {
            if (nickname === "") {
                alert(" .");
            } else if (!nickValidCheck) {
                alert(" .   .");
                return;
            } else {
                const response = await axiosInstance.post(
                    `${process.env.REACT_APP_API_SERVER}/auth/nicknameDuplicate`,
                    { nickname: value }
                );
                alert(response.data.msg);
                if (!response.data.result) {
                    setNickDuplicateCheck(false);
                } else {
                    setNickDuplicateCheck(true);
                }
            }
        }
    };

    const handleRegister = async (e) => {
        e.preventDefault();
        if (!emailDuplicateCheck) {
            alert("  .");
        } else if (!nickDuplicateCheck) {
            alert("  .");
        } else if (
            emailValidCheck &&
            emailDuplicateCheck &&
            nickValidCheck &&
            nickDuplicateCheck &&
            pwValidCheck &&
            confirmPw
        ) {
            const callback = (success, message) => {
                if (success) {
                    closeModal();
                    alert(message);
                }
            };
            //    , ,    
            dispatch(registerUser(email, password, nickname, callback));
        }
    };

    useEffect(() => {
        //   
        setEmailDuplicateCheck(false);
        const emailRegExp =
            /^[A-Za-z0-9]([-_.]?[A-Za-z0-9])*@[A-Za-z0-9]([-_.]?[A-Za-z0-9])*\.[A-Za-z]{2,3}$/;
        if (!emailRegExp.test(email)) {
            setEmailValidCheck(false);
            setEmailValidMessage("   .");
        } else {
            setEmailValidCheck(true);
            setEmailValidMessage("");
        }
    }, [email]);

    useEffect(() => {
        //   
        setNickDuplicateCheck(false);
        const nickRegExp = /^(?=.*[a-z0-9-])[a-z0-9-]{2,8}$/;
        if (!nickRegExp.test(nickname)) {
            setNickValidCheck(false);
            setNickValidMessage(
                "     2~8  ."
            );
        } else {
            setNickValidCheck(true);
            setNickValidMessage("");
        }
    }, [nickname]);

    useEffect(() => {
        //   
        const pwRegExp = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{6,16}$/;
        if (!pwRegExp.test(password)) {
            setPwValidCheck(false);
            setPwValidMessage(
                "      6~16  ."
            );
        } else {
            setPwValidCheck(true);
            setPwValidMessage("   .");
        }
    }, [password]);

    useEffect(() => {
        //   
        if (checkPassword === "") {
            setPwConfirmMessage("    .");
        } else if (password !== checkPassword) {
            setConfirmPw(false);
            setPwConfirmMessage("  .");
        } else {
            setConfirmPw(true);
            setPwConfirmMessage(" .");
        }
    }, [password, checkPassword]);
    return (
        <div>
            <h2></h2>
            <form onSubmit={handleRegister}>
                <div>
                    <input
                        type="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        placeholder=""
                    />
                    <button
                        type="button"
                        onClick={() => checkDuplicate("email", email)} //     email type   
                    >
                        
                    </button>
                    <p>{emailValidMessage}</p>
                </div>
                <div>
                    <input
                        type="text"
                        value={nickname}
                        onChange={(e) => setNickname(e.target.value)}
                        placeholder=""
                    />
                    <button
                        type="button"
                        onClick={() => checkDuplicate("nickname", nickname)} //     nickname type   
                    >
                        
                    </button>
                    <p>{nickValidMessage}</p>
                </div>
                <input
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    placeholder=""
                />
                <p>{pwValidMessage}</p>
                <input
                    type="password"
                    value={checkPassword}
                    onChange={(e) => setCheckPassword(e.target.value)}
                    placeholder=" "
                />
                <p>{pwConfirmMessage}</p>
                <button type="submit"></button>
            </form>
        </div>
    );
};

export default RegisterModalContent;
